<template>
<div class="uk-section uk-height-viewport uk-padding uk-margin uk-section-muted  uk-margin-remove-top">
  <div class="uk-container uk-margin-large uk-padding-large">
    <div class="uk-padding uk-margin uk-position-center uk-width-1-1">
      <div class="uk-alert-danger" uk-alert v-if="error">{{ error }}</div>
      <div v-if="loading"  class="uk-position-center">
        <div class="loader"></div>
      </div>
    </div>
      <div class="uk-position-center uk-margin  uk-margin-large-top"  v-if="loading == false">
      <line-chart :chart-data="datacollection" :style="{'width': '650px','height' : '650px'}" class=""></line-chart>
    </div>
  </div>
</div>
</template>

<script>
  import LineChart from '../../assets/LineChart.js'
  import ArticleService from '../../Service/ArticleService'

  export default {
    components: {
      LineChart
    },
    data () {
      return {
        datacollection: null,
        articles : [],
        colors: [
            "red",
            "blue",
            "green",
        ],
        loading : false,
        error : ''
      }
      
    },

    async created() {
    },
    async mounted () {

      this.loading = true
      this.articles = await ArticleService.getArticle();

      this.fillData()
      let object = {
              label: [],
              backgroundColor: [],
              data: []
      }
       for (let index = 0; index < this.articles.length; index++) {
        this.datacollection.labels.push(this.articles[index].Name)
        object.data.push(this.articles[index].Qte)
        object.backgroundColor.push(this.getRandomColor())

      }
      
      this.datacollection.datasets.push(object)
        this.loading = false
    },
    methods: {
      fillData () {
        this.datacollection = {
         labels: [],
         datasets: [],
         backgroundColors: []
        }
      },
      getRandomColor() {
        var letters = '0123456789ABCDEF';
        var color = '#';
        for (var i = 0; i < 6; i++) {
            color += letters[Math.floor(Math.random() * 16)];
        }
        return color;
    }
    }
  }
</script>

