<template>
    <div>
        <div v-if="loading"  class="uk-position-center">
            <div class="load"></div>
        </div>
        <div class="uk-form-stacked uk-card blueBar" v-if="loading == false">
            <div class="uk-form-stacked uk-margin uk-padding ">
                <label>Creation d'une fiche client : </label>
                <div class="uk-margin">
                    <label class="uk-form-label uk-margin" for="form-stacked-text  ">Nom et prenom : </label>
                    <div class="uk-form-controls">
                        <input class="uk-input uk-form-width-large uk-width-1-1" id="form-stacked-text" type="text"  v-model="Client"  placeholder="Nom et pernom du client ..." required>
                    </div>
                </div>
                <div class="uk-margin">
                    <label class="uk-form-label uk-margin" for="form-stacked-text  ">CIN : </label>
                    <div class="uk-form-controls">
                        <input class="uk-input uk-form-width-large uk-width-1-1" id="form-stacked-text" type="number"  v-model="Cin"  placeholder="Numro de carte d'identi national ..." required>
                    </div>
                </div>
                <div class="uk-margin">
                    <label class="uk-form-label uk-margin" for="form-stacked-text  ">Numro du telephone : </label>
                    <div class="uk-form-controls">
                        <input class="uk-input uk-form-width-large uk-width-1-1" id="form-stacked-text" type="number"  v-model="Tel"  placeholder="Telephone ..." required>
                    </div>
                </div>
                <div class="uk-margin">
                    <label class="uk-form-label uk-margin" for="form-stacked-text  ">Adresse : </label>
                    <div class="uk-form-controls">
                        <input class="uk-input uk-form-width-large uk-width-1-1" id="form-stacked-text" type="text"  v-model="Adresse"  placeholder="Adresse ..." required>
                    </div>
                </div>
                <div><br>
                    <button v-on:click="CreateClient" class="uk-button uk-button-primary"> Ajouter <i class="fa fa-send-o uk-margin-left"></i></button>
                </div>
                <div class="uk-alert uk-alert-primary uk-text-center" v-if="msg"><i class="fa fa-plus-circle "></i>{{ msg }}</div>
            </div>
        </div>
    </div>
</template>

<script>
import ClientService from '../../../Service/ClientService'
export default {
    name : 'FicheClient',
    data () {
        return {
            Client : '',
            Cin : '',
            Tel : '',
            Adresse :'',
            msg : '',
            loading : false
        }
    },mounted() {

    },
    methods : {
        async CreateClient () {
            this.loading = true
            await ClientService.setClient(this.Client,this.Cin,this.Tel,this.Adresse)
            this.msg = ' Client  t ajouter ! '
            this.Client = ''
            this.Tel = ''
            this.Cin = ''
            this.Adresse =''
            this.loading = false
        }
    }

}
</script>

<style>
.blueBar{
    border-left: 5px rgba(33,150,243) solid;    
}
</style>
