<template>
    <div class="uk-section uk-height-viewport uk-padding uk-margin uk-margin-remove-top">
    <div class="uk-container uk-margin-large uk-padding-large">
        <div class="uk-padding uk-margin uk-position-center uk-width-1-1">
        <div class="uk-alert-danger" uk-alert v-if="error">{{ error }}</div>
            <div v-if="loading"  class="uk-position-center">
                <div class="loader"></div>
            </div>
        </div>
        <div class="uk-margin uk-grid" uk-grid   v-if="loading == false">
            <div class="uk-width-1-3">
                <ul class="uk-list">
                    Liste des Article : <br>
                    <li v-for=" x in article" :key="x.id" ><span  class="uk-button uk-button-default btn-block" v-on:click="calc(x.Name)" > {{x.Name}} </span></li>
                    <li> <span  class="uk-button uk-button-default btn-block" v-on:click="Tot()" >Totale  </span></li>
                </ul>
            </div>
            <div class="uk-width-2-3" v-if="show">
                <table class="uk-table uk-table-hover uk-table-divider" >
                    <thead>
                        <tr>
                            <th>Article </th>
                            <th>Prix de l'unit </th>
                            <th>Quantit</th>
                            <th>Prix Totale De la commande</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="a in tmpAchat" :key="a.id">
                            <td> {{a.Article}}</td>
                            <td> {{a.Prix}} DT </td>
                            <td> {{a.Qte }} </td>
                            <td> {{a.PrixTot}} DT </td>
                            
                        </tr>
        
                    </tbody>
                </table>
                <div class="uk-alert uk-alert-danger">
                    Montant Total des achats : {{ prixAchatTot }} DT
                </div>
                <br>
                <table class="uk-table uk-table-hover uk-table-divider" >
                    <thead>
                        <tr>
                            <th>Article </th>
                            <th>Prix de vente</th>
                            <th>Quantit</th>


                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="v in tmpVente" :key="v.id">
                            <td> {{v.Article}}</td>
                            <td> {{v.Prix}} DT </td>
                            <td> {{v.Qte }} </td>

                        </tr>
        
                    </tbody>
                </table>
                <div class="uk-alert uk-alert-danger">
                    Montant totale des ventes : {{ prixVenteTot }} DT
                </div>
                <div class="uk-alert uk-alert-primary">
                    Rapport vente-achat : {{ tot }} DT
                </div>
            </div>

        </div>
    </div>
    </div>
</template>

<script>
import CommandeService from '../../Service/CommandeService.js'
import VenteService from '../../Service/VenteService.js'
import ArticleService from '../../Service/ArticleService.js'

export default {
    name : 'Calcule',
    data (){
        return{
            loading : false,
            error : '',
            vente : [],
            achat : [],
            article : [],
            tmpAchat :[],
            tmpVente : [],
            prixAchatTot : '',
            prixVenteTot : '',
            show : false,
            tot : ''
        }
    },
    mounted() {

    },
    async created() {        
        this.loading = true
        
        try{
            this.achat = await CommandeService.getCommande()
            this.vente = await VenteService.getContrat()
            this.article = await ArticleService.getArticle()

            this.loading = false        
        }catch(err){
            this.error = 'Aucun accs  Internet'
            this.loading = false
        }
    },
    methods: {
        calc : function(name){
            this.show = true
            this.tmpAchat = []
            this.tmpVente = []
            this.prixAchatTot = 0
            this.prixVenteTot = 0

            for (let i = 0; i < this.achat.length; i++) {
                if( this.achat[i].Article == name ){
                    this.tmpAchat.push(this.achat[i])
                }
            }
            let result = 0
            for (let j = 0; j < this.tmpAchat.length; j++) {
                result = result + parseInt(this.tmpAchat[j].PrixTot)
                this.prixAchatTot = result 
            }
            for (let i = 0; i < this.vente.length; i++) {
                if( this.vente[i].Article == name ){
                    this.tmpVente.push(this.vente[i])
                }
            }
            let result2 = 0
            for (let j = 0; j < this.tmpVente.length; j++) {
                result2 = result2 + parseInt(this.tmpVente[j].Prix)
                this.prixVenteTot = result2  
            }
            this.tot = this.prixVenteTot - this.prixAchatTot  
        },
        Tot : function(){

            this.show = true 
            this.tmpAchat = this.achat
            this.tmpVente = this.vente
            let result = 0
            for (let j = 0; j < this.tmpAchat.length; j++) {
                result = result + parseInt(this.tmpAchat[j].PrixTot)
                this.prixAchatTot = result 
            }
            let result2 = 0
            for (let j = 0; j < this.tmpVente.length; j++) {
                result2 = result2 + parseInt(this.tmpVente[j].Prix)
                this.prixVenteTot = result2  
            }
            this.tot = this.prixVenteTot - this.prixAchatTot  
        }   
    }
}
</script>

<style>

</style>
