<template>
<div class="uk-section uk-height-viewport uk-padding uk-margin uk-section-muted uk-margin-remove-top">
  <div class="">
    <div class="uk-padding uk-margin uk-margin-left uk-position-center uk-width-1-2">
      <div class="uk-alert-danger" uk-alert v-if="error">{{ error }}</div>
      <div v-if="loading"  class="uk-position-center">
        <div class="loader"></div>
      </div>
    </div>
    <div class="uk-grid" uk-grid  v-if="loading == false">
    <div class="uk-width-1-4  uk-margin-large uk-margin-large-top ">
            <p> Liste des Article : </p>
            <ul class="uk-list uk-list-striped">
                <li class="" 
                    v-for="com in articles" :value="com.Name" :key="com.id" >
                    <a v-on:click="getStat(com.Name)"> 
                        {{com.Name}}
                    </a>
                    <span class="pull-right">
                        <span class="uk-badge">
                            Disponible : {{com.Qte}}
                        </span>
                    </span>
                </li>
            </ul>
        </div>
        <div class="small uk-width-1-2" >
            <span class="uk-align-center uk-text-center"> Rapport du vente - achat de l'article : {{ Name }}</span> 
            <div class="uk-height-max-small uk-width-3-4 uk-text-center uk-align-center">
                <MChart :chart-data="datacollection" :styles="{height: '250px'}" ></MChart>
            </div>
        </div>
    </div>
  </div>
</div>
</template>

<script>
import MChart from '../../assets/MChart.js'
import VenteService from '../../Service/VenteService';
import CommandeService from '../../Service/CommandeService'
import ArticleService from '../../Service/ArticleService.js';


  export default {
    name : 'StatistiqueVente',
    components: {
        MChart
    },
    data () {
        return {
            loading : false,
            vente : [],
            commande : [],
            articles : [],
            totVente : '',
            totAchat :'',
            datacollection: null,
            error : '',
            commandetmp :[],
            ventetmp : [],
            Name : ''
        }      
    },

    async created() {

    },
     async mounted () {
        try{
            this.loading = true
            this.vente = await VenteService.getContrat()
            this.commande = await CommandeService.getCommande()
            this.articles = await ArticleService.getArticle()
            this.loading = false
        }catch(err){
            this.error = 'Aucun accs  Internet';
            this.loading = false
        }
    },
    methods: {
        getStat(nom){
            this.Name = nom
            //this.ventetmp = []
            //this.commandetmp = []
             
            //this.commandetmp = this.commande
            //this.commandetmp = this.commande.filter(commande => {
            //    return commande.Article.toString().toLowerCase().includes(nom.toLowerCase())
            //})

            for(let j = 0 ; j<this.commande.length;j++ ){
                if(this.commande[j].Article === nom ){
                    this.commandetmp.push(this.commande[j])
                }
            }
            for(let j = 0 ; j < this.vente.length;j++ ){
                if(this.vente[j].Article === nom ){
                    this.ventetmp.push(this.vente[j])
                }
            }
            // for(let i  = 0 ; i <this.commandetmp.length ; i++){
            //     this.totAchat = this.totAchat + this.commandetmp[i].Qte
            // }
            //this.ventetmp = this.vente
            //this.ventetmp = this.vente.filter(vente => {
            //    return vente.Article.toString().toLowerCase().includes(nom.toLowerCase())
            //})

            // for(let i  = 0 ; i <this.ventetmp.length ; i++){
            //     this.totVente = this.totVente + this.ventetmp[i].Qte
            // }
            this.fillData()
            let object = {
                label: [],
                backgroundColor: [],
                data: []
            }
            // if(this.ventetmp ===  [] ){
            //    this.totVente = 0
            // }
            //this.commandetmp = []
                       
            for(let i  = 0 ; i <this.commandetmp.length ; i++){
                object.data.push(this.commandetmp[i].Qte)
                object.backgroundColor.push("#20c997");
                this.datacollection.labels.push("Achat")

            }
            for(let i  = 0 ; i <this.ventetmp.length ; i++){
                object.data.push(this.ventetmp[i].Qte)
                object.backgroundColor.push("#1e87f0");
                this.datacollection.labels.push("Vente")

            }

            this.datacollection.datasets.push(object)
            this.ventetmp = []
            this.commandetmp = []
        },
        
        fillData () {
            this.datacollection = {
                labels: [],
                datasets: [],
                backgroundColors: []
            }
        },

        getRandomColor() {
            var letters = '0123456789ABCDEF';
            var color = '#';
            for (var i = 0; i < 6; i++) {
                color += letters[Math.floor(Math.random() * 16)];
            }
            return color;
        },
    }
}
</script>

<style>
  .small {
    max-width: 300px;
    margin:  10px auto;
  }
</style>