import axios from 'axios';

let url = 'api/vente/';
let urls = 'api/Vente/Client/'

class VenteService {

    static SetVente(Client,Cin,Article,Qte,Paiement,Type,Taux,Prix){
        return axios.post(url,{
            Client,
            Cin,
            Article,
            Qte,
            Paiement,
            Type,
            Taux,
            Prix
        })
    }
    static GetClientContrat(Client){
        return new Promise(async (resolve,reject) =>{
            try{
                const res = await axios.get(`${urls}${Client}`);
                const data = res.data;
                resolve(
                    data.map(Contrat =>({
                        id : Contrat._id,
                        Article : Contrat.Article,
                        Client : Contrat.Client,
                        Prix : Contrat.Prix,
                        Paiement : Contrat.Paiement,                        
                        Qte : Contrat.Qte,
                        Type : Contrat.Type,
                        Taux : Contrat.Taux,
                        CreatedAt : new Date( Contrat.CreatedAt),
                        //CR : moment(commande.CreatedAt).format("MM-DD-YYYY")

                    }))
                )
            }catch(err){
                reject(err);
            }
        })
    }
    static getContrat(){
        return new Promise(async (resolve,reject) =>{
            try{
                const res = await axios.get(url);
                const data = res.data;
                resolve(
                    data.map(Contrat =>({
                        id : Contrat._id,
                        Client : Contrat.Client,
                        Article : Contrat.Article,
                        Prix : Contrat.Prix,
                        Paiement : Contrat.Paiement,                        
                        Qte : Contrat.Qte,
                        Type : Contrat.Type,
                        Taux : Contrat.Taux,
                        CreatedAt : new Date( Contrat.CreatedAt),
                        //CR : moment(commande.CreatedAt).format("MM-DD-YYYY")

                    }))
                )
            }catch(err){
                reject(err);
            }
        })
    }
    static deleteVente(id){
        return axios.delete(`${url}${id}`);
    }
    static getOneArticle(id){
        return new Promise(async (resolve,reject) =>{
            try{
                const res = await axios.get(`${url}${id}`);
                const data = res.data;
                resolve(
                    data.map(Contrat =>({
                        id : Contrat._id,
                        Client : Contrat.Client,
                        Article : Contrat.Article,
                        Prix : Contrat.Prix,
                        Paiement : Contrat.Paiement,                        
                        Qte : Contrat.Qte,
                        Type : Contrat.Type,
                        Taux : Contrat.Taux,
                        CreatedAt : new Date( Contrat.CreatedAt),
                    }))
                )
            }catch(err){
                reject(err);
            }
        })
    }

}

export default VenteService
