import Vue from 'vue'
import Router from 'vue-router'
import home from '../views/home'
import logout from '../views/logout'
import articles from '../components/ArticlesComponents/articles'
import OneArticle from '../components/ArticlesComponents/OneArticle'
import create from '../components/ArticlesComponents/create'
import filtre from '../components/ArticlesComponents/filtre'
import stat from '../components/ArticlesComponents/stat'
import register from '../components/LoginComponents/register'
import login from '../components/LoginComponents/login'
import Commande from '../components/CommandeComponents/Commande'
import Statistique from '../components/CommandeComponents/Statistique'
import Vente from '../components/VenteComponents/Vente'
import AddContrat from '../components/VenteComponents/Menu/AddContrat'
import ListContrat from '../components/VenteComponents/ListContrat'
import FindOneContrat from '../components/VenteComponents/FindOneContrat'
import ListClient from '../components/VenteComponents/Menu/ListClient' 
import ClientContrat from '../components/VenteComponents/Menu/ClientContrat' 
import StatistiqueVente from '../components/VenteComponents/StatistiqueVente'
import statClient from '../components/VenteComponents/statClient'
import Calcule from '../components/ComptablititeComponenet/Calcule'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },{
        path : '/article',
        name: 'articles',
        component: articles
    },{
      path :'/article/:id',
      name : 'OneArticle',
      component : OneArticle
    },{
      path :'/create',
      name : 'create',
      component : create
    },{
      path :'/filtre',
      name : 'filtre',
      component : filtre
    },{
      path :'/stat',
      name : 'stat',
      component : stat
    },{
      path :'/login',
      name : 'login',
      component : login
    },{
      path :'/register',
      name : 'register',
      component : register
    },{
      path :'/logout',
      name : 'logout',
      component : logout
    },{
      path :'/Commande',
      name : 'Commande',
      component : Commande
    },{
      path :'/Statistique',
      name : 'Statistique',
      component : Statistique
    }
    ,{
      path :'/Vente',
      name : 'Vente',
      component : Vente
    }
    ,{
      path :'/AddContrat',
      name : 'AddContrat',
      component : AddContrat
    },{
      path :'/ListContrat',
      name : 'ListContrat',
      component : ListContrat
    }
    ,{
      path :'/Vente/:id',
      name : 'FindOneContrat',
      component : FindOneContrat
    }
    ,{
      path :'/ListClient',
      name : 'ListClient',
      component : ListClient
    }
    ,{
      path :'/ClientContrat/:Client',
      name : 'ClientContrat' ,
      component : ClientContrat
    },
    {
      path : '/StatistiqueVente',
      name : 'StatistiqueVente',
      component : StatistiqueVente
    },
    {
      path : '/statClient',
      name : 'statClient',
      component : statClient
    },
    {
      path :'/Calcule',
      name :'Calcule',
      component : Calcule
    }
  ]
})
