<template>
<div class="uk-section uk-height-viewport uk-padding uk-margin uk-section-muted  uk-margin-remove-top">
  <div class="uk-container uk-margin-large uk-padding-large">
    <div class="uk-padding uk-margin uk-position-center uk-width-1-1">
      <div class="uk-alert-danger" uk-alert v-if="error">{{ error }}</div>
      <div v-if="loading"  class="uk-position-center">
        <div class="loader"></div>
      </div>
    </div>
    <div class="uk-position-center uk-margin "  v-if="loading == false">
      <AreaChart :chart-data="datacollection" :style="{'width': '650px','height' : '650px'}" class=""></AreaChart>
    </div>
  </div>
</div>
</template>

<script>
import AreaChart from '../../assets/AreaChart.js'
import ClientService from '../../Service/ClientService'

  export default {
    name : 'statClient',
    components: {
      AreaChart
    },
    data () {
      return {
        datacollection: null,
        articles : [],
        colors: [
            "red",
            "blue",
            "green",
        ],
        loading : false,
        error : ''
      }
      
    },

    async created() {

    },
    async mounted () {

      this.loading = true
      this.articles = await ClientService.GetClients();

      this.fillData()
      let object = {
              label: '',
              backgroundColor: ['#20c997'],
              data: []
      }

      // for (let index = 0; index < this.articles.length; index++) {

      //  let mydate = this.articles[index].CreatedAt.getDate()+"/"+this.articles[index].CreatedAt.getMonth()+"/"+this.articles[index].CreatedAt.getFullYear()
      //  this.datacollection.labels.push(mydate)
      //  object.data.push(this.articles.length)
      //  object.backgroundColor.push(this.getRandomColor())
      // }
      
      let ob = {
        na :[],
        md :[]
      }


// "01/11/2018,02/12/2018,04/12/2018,04/12/2018,05/12/2018,05/12/2018,05/12/2018,09/12/2018,09/12/2018".filter(x=<{x=})   
//



      for( let index = 0; index < this.articles.length; index++){
        let mydate = this.articles[index].CreatedAt.getDate()+"/"+this.articles[index].CreatedAt.getMonth()+"/"+this.articles[index].CreatedAt.getFullYear()
        ob.na.push(mydate)
//        ob.na.push(this.articles[index].Client)
      }

    let obs = {
        d : [],
    }
    let xx  = {
        x : [],
        y : []

    }
    let b = 0 
    for( let i = 0 ; i < ob.na.length; i++ ){
        
        if( ob.na[i] == ob.na[i+1]){
            b = 0
        }else{
            obs.d.push(ob.na[i])
        }
    }
    for( let i = 0 ; i < obs.d.length; i++){
        let a = obs.d[i]
        xx.x.push(a)
        this.datacollection.labels.push(a)
        for( let j = 0 ; j < ob.na.length; j++){
            if( a == ob.na[j]){
                b++
            }
        }

        object.data.push(b)
    }
      this.datacollection.datasets.push(object)

      this.loading = false

    },
    methods: {
      fillData () {
        this.datacollection = {
         labels: [],
         datasets: [{}],
         backgroundColor: '#ccc'
        }
      },
      getRandomColor() {
        var letters = '0123456789ABCDEF';
        var color = '#';
        for (var i = 0; i < 6; i++) {
            color += letters[Math.floor(Math.random() * 16)];
        }
        return color;
    }
    }
  }
</script>

<style>
  .small {


  }
</style>