<template>
    <div class="uk-section uk-height-viewport uk-padding uk-margin uk-margin-remove-top ">
        <div class="uk-container">
            <div class="alert alert-danger col-12" v-if="error">{{ error }}</div>
            <div v-if="loading"  class="uk-position-center">
                <div class="loader"></div>
            </div> 
            <div class="" v-if="loading == false" >
                <ul uk-tab="animation: uk-animation-slide-left-medium, uk-animation-slide-right-medium">

                    <li class="uk-text-right"><router-link to="/ListContrat" class="uk-margin-right">Liste des Contrats</router-link></li>
                    
                    <li class="uk-text-right"><router-link to="/AddContrat" class="uk-margin-right">Ajouter un contrat </router-link></li>

                    <li class="uk-text-right"><router-link to="/Vente" class="uk-margin-right">Ajouter un client </router-link></li>

                    <li class="uk-text-right"><router-link to="/ListClient" class="uk-margin-right">Liste des Clients</router-link></li>

                </ul>

                <ul class="uk-switcher uk-margin">
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li> 

                </ul>
                <div v-for="C in Contrat" :key="C.id">

                    <article class="uk-article uk-margin">
          
                        <h2 class="uk-article-Lead"><router-link :to="'Vente/'+C.id" class="uk-text-capitalize" > {{ C.Client }} - {{ C.Article }}</router-link></h2>

                        <p class="uk-article-meta">Fait le <a href="#"> </a> {{`${C.CreatedAt.getDate()}/${C.CreatedAt.getMonth()}/${C.CreatedAt.getFullYear()}`}}</p>

                        <p> La vente de {{C.Qte}} {{ C.Article }} pour le client {{C.Client}} <br> Paiement <span v-if="C.Type"> par </span>{{ C.Paiement }} <span v-if="C.Taux != 0 ">de {{ C.Taux }} Dinars Sur  {{ C.Type }} Mois .</span><br>Prix Total :  {{ C.Prix }} Dinars </p>

                    </article>
                    <div class="uk-divider-icon"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import VenteService from '../../Service/VenteService';

export default {
    name:'ListContrat',
    data () {
        return {
            loading: false,
            error : '',
            Client :'',
            Article :'',
            Contrat : []
            
        }
    }, mounted() {
    },
    async created(){
        
        this.loading = true
        try{
            this.Contrat = await VenteService.getContrat()
            this.loading = false
        }catch(error){
            this.error = 'Aucun accs  Internet'
            this.loading = false
        }

    }
}
</script>

