<template>
    <div class="uk-section uk-height-viewport uk-padding uk-margin uk-section-muted uk-margin-remove-top">
        <div class="uk-container">
            <div class="uk-padding uk-margin uk-margin-left uk-position-center uk-width-1-2">
                <div class="uk-alert-danger" uk-alert v-if="error">{{ error }}</div>
                
                <div v-if="loading"  class="uk-position-center">
                    <div class="loader"></div>
                </div>
            </div>
            <div class="uk-width-1-1 "  v-if="loading == false">
             <div class="uk-form-stacked uk-card uk-card-default">
                    <div class="uk-form-stacked uk-margin uk-padding ">
                        <div class="uk-margin">
                            <label class="uk-form-label uk-margin  uk-padding-small uk-padding-remove-left" for="form-stacked-text  ">Nom et pernom : </label>
                            <div class="uk-form-controls">
                                <input class="uk-input uk-form-width-large uk-width-1-1" id="form-stacked-text" type="text"  v-model="Name"  placeholder="Nom et Prenom ..." required>
                            </div>
                        </div>
                        <div class="uk-margin">
                            <label class="uk-form-label uk-margin uk-padding-small uk-padding-remove-left" for="form-stacked-text  ">Mail : </label>
                            <div class="uk-form-controls">
                                <input class="uk-input uk-form-width-large uk-width-1-1" id="form-stacked-text" type="text"  v-model="Mail"  placeholder="Adress mail ..." required>
                            </div>
                        </div>
                        <div class="uk-margin">
                            <label class="uk-form-label uk-margin uk-padding-small uk-padding-remove-left" for="form-stacked-text"> Mot de passe </label>
                            <div class="uk-form-controls">
                                <input class="uk-input uk-form-width-large uk-width-1-1" id="form-stacked-text" type="password"  v-model="Password"  placeholder="Mot de passe ..." required>
                            </div>
                        </div>
                        <div class="uk-margin">
                            <label class="uk-form-label uk-margin uk-padding-small uk-padding-remove-left" for="form-stacked-text">Confiramtion de mot passe </label>
                            <div class="uk-form-controls">
                                <input class="uk-input uk-form-width-large uk-width-1-1" id="form-stacked-text" type="password"  v-model="verifPassword" v-on:input="Verification"  placeholder="Mot de passe ..." required>
                            </div>
                        </div>
                        <div class="uk-margin uk-grid-small uk-child-width-auto uk-grid">
                            <label> Admin : </label>
                            <label><input class="uk-radio" type="radio" name="IsAdmin" v-model="IsAdmin" v-on:change="check" value="Oui"> Oui </label>
                            <label><input class="uk-radio" type="radio" name="IsAdmin" v-model="IsAdmin" checked v-on:change="check" value="Non"> Non </label>
                        </div>

                        <div><br>
                        <button v-on:click="register" class="uk-button uk-button-primary uk-animation-fade" v-if="verif">Register <i class="fa fa-send-o uk-margin-left"></i></button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import UsersService from '../../Service/UsersService'

export default {
    name : 'register',
    data(){
       return {
            loading: false,
            error:'',
            Name:'',
            Mail:'' ,
            Password:'' ,
            verifPassword:'',
            IsAdmin:'',
            verif:false,
            message:'',
            NotAdmin : false,

        }
    },
      mounted () {
    this.loading = true
    if(localStorage.token && localStorage.id){
      this.LoginId =  localStorage.id
      this.Logintoken =  localStorage.token
    }else{
     this.$router.push('login')
    }
    this.loading = false
  },
    methods : {
        async register(){
            this.loading = true
            if(this.Name == '' || this.Mail == '' || this.Password =='' || this.IsAdmin ==''){
                this.message = 'Tous les champs sont obligatoire ! '
            }         
            let res = await UsersService.createUser(this.Name,this.Mail,this.Password,this.IsAdmin)
            if(res.status == 201 ){
                this.message ='user created'
            }
            if(res.data.data.error){
                this.message = res.data.data.error;
            }
            this.Name = '',
            this.Mail ='',
            this.Password='',
            this.IsAdmin ='',
            this.loading = false
        },
        Verification : function() {
            if(this.verifPassword == this.Password){
                this.verif = true
            }else{
                this.verif = false
            }
        },
        check : function(){
            if(this.IsAdmin == "Non"){
                this.NotAdmin = true
            }else{
                this.NotAdmin = false
            }
        }

    }

}
</script>

