<template><div>
<div v-if="loading"  class="uk-position-center">
    <div class="loader"></div>
</div> 
    <div class="uk-form-stacked uk-card uk-card-default" v-if="loading == false">
        <div class="uk-form-stacked uk-margin uk-padding ">
            <div class="uk-margin">
                <label class="uk-form-label" for="form-stacked-select">Nom du fournisseur :</label>
                <div class="uk-form-controls">
                    <select class="uk-select" id="form-stacked-select" v-model="Fournisseur" v-if="fourinput == false">
                        <option>Intel</option>
                        <option>DELL</option>
                        <option>HP</option>
                        <option>ASUS</option>
                        <option>Lenouvo</option>
                        <option>AMD</option>
                        <option>NEVIDIA</option>
                        <option>MSI</option>
                    </select>
                    <input class="uk-input uk-form-width-large uk-width-1-1" placeholder="Entrer le nom du fournisseur ...." id="form-stacked-text" type="text"  v-model="Fournisseur" v-if="fourinput">

                </div>
            </div>
            
 <!--           <p class="uk-button uk-button-link" v-on:click="FourinputTog">Si le fournissuer n'existe pas cliquer ici </p> -->


            <div class="uk-margin">
                <label class="uk-form-label" for="form-stacked-select">Liste des article :</label>
                <div class="uk-form-controls">
                    <select class="uk-select" id="form-stacked-select" v-model="Article" v-if="articleinput == false">
                        <option>Ordinateur Bureau </option>
                        <option>Pc portable</option>
                        <option>Clavier</option>
                        <option>Souris</option>
                        <option>Carte graphique</option>
                        <option>RAM</option>
                        <option>Disque Dur</option>
                        <option>Haut parleur</option>
                        <option>Ecran</option>
                        <option>Tablette</option>
                        <option>Processeur</option>
                        <option>Appareil photo </option>
                        <option>Camera</option>
                        <option>Imprimente</option>
                        <option>Scanner</option>

                    </select>
                    <input class="uk-input uk-form-width-large uk-width-1-1" id="form-stacked-text" type="text" placeholder="Entrer le nom de l'article ..."  v-model="Article" v-if="articleinput">

                </div>
            </div>
            <p class="uk-button uk-button-link" v-on:click="articleinputTog">Si l'article ou le fournisseur n'existe pas cliquer ici </p>
            <div class="uk-margin">
                <label class="uk-form-label uk-margin" for="form-stacked-text">Desciption de l'article : </label>
                <div class="uk-form-controls">
                    <textarea class="uk-textarea  uk-form-width-large uk-width-1-1 "  rows="3"  id="form-stacked-text" type="text"  v-model="Desc" placeholder="Desciption ..." required></textarea>
                </div>
            </div>


            <div class="uk-margin">
                <label class="uk-form-label uk-margin" for="form-stacked-text">Prix de l'unit en dinar :  </label>
                <div class="uk-form-controls">
                    <input class="uk-input uk-form-width-large uk-width-1-1" id="form-stacked-text" type="number"  v-on:input="change"   v-model="PrixUnit"  placeholder="Prix de l'unit" required>
                </div>
            </div>
            
            <div class="uk-margin">
                <label class="uk-form-label uk-margin" for="form-stacked-text">Quantit  commander : </label>
                <div class="uk-form-controls">
                    <input class="uk-input uk-form-width-large uk-width-1-1" id="form-stacked-text" type="number" v-on:input="change" v-model="Qte"  placeholder="Quantit ..." required>
                </div>
            </div>
            <div class="uk-margin">
                <label class="uk-form-label uk-margin" for="form-stacked-text">Prix :  </label>
                <div class="uk-form-controls">
                    <input class="uk-input uk-form-width-large uk-width-1-1" id="form-stacked-text" type="number"  v-model="PrixTot" disabled>
                </div>
            </div>

            <div><br>
                <button class="uk-button uk-button-primary" v-on:click="Commander">commander <i class="fa fa-send-o uk-margin-left"></i></button>
            </div>
        </div>
    </div>  
</div>
</template>

<script>
import CommandeService from '../../../Service/CommandeService';
export default {
    name: 'AddCommande',
    data () {
        return {
            Fournisseur :'',
            Article : '',
            Qte : '',
            PrixTot : '',
            PrixUnit : '',
            message : '',
            articleinput : false,
            fourinput : false,
            loading : false,
            Desc : ''
        }
    },methods: {
        change : function(){
            if(this.PrixUnit <0 || this.Qte < 0 ){
                this.PrixUnit = 0
                this.Qte = 0
            }
            this.PrixTot = this.PrixUnit * this.Qte
        },
        articleinputTog : function(){
            this.articleinput = !this.articleinput
            this.fourinput = !this.fourinput

        },
        FourinputTog : function(){
        },
        async Commander (){
            this.loading = true
            if(this.Fournisseur == '' || this.Article == ''|| this.Qte == ''  || this.PrixUnit == '' || this.Desc ==''  ){
                this.message = 'les champs sont obligatoire'
            }else{
                this.PrixTot = this.PrixUnit * this.Qte
                await CommandeService.createCommande(this.Fournisseur,this.Article,this.Desc,this.PrixUnit,this.Qte,this.PrixTot);
                this.Fournisseur = ''
                this.Article = ''
                this.Qte = '',
                this.Desc = ''
                this.PrixUnit = '' 
                this.loading = false
            }

        }
    }

}
</script>

