<template>
    <div class="uk-section uk-height-viewport uk-padding uk-margin uk-margin-remove-top ">
        <div class="uk-container">
            <div class="alert alert-danger col-12" v-if="error">{{ error }}</div>
            <div v-if="loading"  class="uk-position-center">
                <div class="loader"></div>
            </div> 
            <div class="" v-if="loading == false" >
                <ul uk-tab="animation: uk-animation-slide-left-medium, uk-animation-slide-right-medium">


                    <li><router-link to="/ListClient" class="uk-margin-right">Liste des Clients </router-link></li>
                
                    <li class="uk-text-right"><router-link to="/ListContrat" class="uk-margin-right">Liste des Contrats</router-link></li>
                    
                    <li class="uk-text-right"><router-link to="/AddContrat" class="uk-margin-right">Ajouter un contrat </router-link></li>

                    <li><router-link to="/Vente" class="uk-margin-right">Ajouter un client </router-link></li>


                </ul>

                <ul class="uk-switcher uk-margin">
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li> 

                </ul>
                <div v-for="C in Client" :key="C.id">

                    <article class="uk-article uk-margin">
          
                        <h4 class="uk-article-Lead"><router-link :to="'/ClientContrat/'+C.Client" class="uk-text-capitalize" > {{ C.Client }} </router-link></h4>

                        <p class="uk-article-meta">Depuis le : <a href="#"> </a> {{`${C.CreatedAt.getDate()}/${C.CreatedAt.getMonth()}/${C.CreatedAt.getFullYear()}`}}</p>

                        <p>Adresse : {{C.Adresse}} <br> Cin : {{ C.Cin }} - Tel : {{ C.Tel }}</p>

                    </article>
                    <div class="uk-divider-icon"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import ClientService from '../../../Service/ClientService'
export default {
    name : 'ListClient',
    data () {
        return {
            loading : false,
            Client : [],
            error : ''
        }
    },mounted() {
    },
    async created() {
        this.loading = true
        try{ 
            this.Client = await ClientService.GetClients()
            this.loading = false
        }catch(error){
            this.error = 'Aucun accs  Internet'
            this.loading = false
        }
    },
}
</script>

