import axios from 'axios';

let url = 'api/Users/';
let register = 'register';
let login = 'login';


class UsersService {
    
    static login(Mail,Password){
        return axios.post(`${url}${login}`,{
            Mail,
            Password,
        })
        .catch((err) => { 
            err.error 
        }); 
        
    }

    static createUser(FullName,Mail,Password,IsAdmin){
        return axios.post(`${url}${register}`,{
            FullName,
            Mail,
            Password,
            IsAdmin
        })
    }
    
    static updateByUser(id,FullName,Mail,Password){
        return axios.put(`${url}${id}`,{
            FullName,
            Mail,
            Password
        }) 
    }
    static updateByAdmin(id,FullName,Mail,Password,IsAdmin){
        return axios.put(`${url}${id}`,{
            FullName,
            Mail,
            Password,
            IsAdmin
        }) 
    }

    static deleteUser(id){
        return axios.delete(`${url}${id}`);
    }


}

export default UsersService
