<template>
<div>
    <div v-if="loading"  class="uk-position-center">
        <div class="load"></div>
    </div> 
    <div class="uk-card uk-card-default uk-padding">

        <!--<select class="uk-select" v-model="type">
            <option v-for="t in types" v-bind:key="t" :value="t">{{t}} </option>
        </select>-->
        <div class="uk-margin uk-grid-small uk-child-width-auto uk-grid">
            <label>Vous voulez faire la recherche selon : </label>
            <label><input class="uk-radio" type="radio" name="type" v-model="type" value="Article"> Article </label>
            <label><input class="uk-radio" type="radio" name="type" v-model="type" value="Fournisseur"> Fournisseur </label>
            <label><input class="uk-radio" type="radio" name="type" v-model="type" value="Quantit"> Quantit </label>
            <label><input class="uk-radio" type="radio" name="type" v-model="type" value="Prix Totale"> Prix Totale </label>
        </div>
        
        <form class="uk-search uk-search-large uk-alert uk-width-1-1" uk-alert>
            <span uk-search-icon></span>
            <input class="uk-search-input " type="search" placeholder="Recherche ..." v-model="search"  v-on:input="searchCommande">
        </form>
        <br>
        <div v-if="msg">
            <div class="uk-alert uk-alert-primary" uk-alert>
                {{ msg}}
            </div>
        </div>
        <hr>
    </div>
        <div v-if="commande" class="uk-card uk-card-default uk-margin uk-padding">
            <div v-if="commandetmp" class="uk-width-1-1">
                <div v-if="show">
                    <ul uk-accordion>
                    <li v-for="com in commandetmp" v-bind:key="com.id">
                        <a class="uk-accordion-title uk-text-primary" href="#"> {{com.Article}} - {{com.Fournisseur}} - {{ com.PrixTot}} Dinars <span class="pull-right uk-margin-right"> {{ `${com.CreatedAt.getDate()}/${com.CreatedAt.getMonth()}/${com.CreatedAt.getFullYear()}`}} </span></a>
                        <div class="uk-accordion-content ">
                            <p>Nom de l'article : {{ com.Article }}</p>
                            <p>Fournisseur : {{ com.Fournisseur }}</p>
                            <p>Quantit : {{ com.Qte }}</p>
                            <p>Prix de l'article : {{ com.Prix }} Dinars</p>
                            <p>Prix Totale  : {{ com.PrixTot }} Dinars</p>
                            <button class="uk-button uk-button-danger" v-on:click="deleteCommande(com.id)">Supprimer </button>
                        </div>
                        <hr>
                    </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import CommandeService from '../../../Service/CommandeService';
 
export default {
    name : 'FindCommande',
    data(){
        return{
            search : '',
            types : ["Article" ,"Fournisseur" , "Date" , "Prix Totale", "Quantit"],
            type : 'Filtre',
            commande : [],
            commandetmp : [],
            show : false,
            msg : '',
            loading : false
        }
    },
    async created () {
        try{
            this.commande = await CommandeService.getCommande();
        }catch(err){
            this.error = err
        }
    },
    methods: {
        async searchCommande() {
            if(this.type == 'Filtre'){
                this.search =''
                this.msg = 'le champs de filtrage est obligatoire !'
            }
            /*if(this.type == 'Quantit' || this.type == 'Prix Totale'){
                if(  !Number(this.search) ){
                    this.search = ''
                    this.msg = ' les lettres sont pas autoris'
                }
            }*/
            else{
                this.search = this.search.toUpperCase()
                this.msg = ''
                this.show = true
                let ts = this.type
                let test = {"Prix Totale":"PrixTot","Fournisseur":"Fournisseur","Quantit":"Qte","Article":"Article"}
                this.commandetmp = this.commande
                this.commandetmp = this.commande.filter(commande => {
                    return commande[test[ts]].toString().toLowerCase().includes(this.search.toLowerCase())
                })
            }
            if(this.search == '' || this.commandetmp == ''){
                    this.show = false
                    this.commandetmp = this.commande
            }
            
        },
        async deleteCommande(id) {
            this.loading = true
            await CommandeService.DeleteCommande(id)
            this.commandetmp = await CommandeService.getCommande()
            this.loading = false
        }
        
        // async searchCommande  () {
        //     this.commande = await CommandeService.getSearch(this.type,this.search)  
        // }
    }
}
</script>

