<template>
    <div class="uk-section uk-height-viewport uk-padding uk-margin uk-margin-remove-top ">
        <div class="uk-container">
            <div class="alert alert-danger col-12" v-if="error">{{ error }}</div>
            <div v-if="loading"  class="uk-position-center">
                <div class="loader"></div>
            </div> 
            <div class="" v-if="loading == false" >
                <ul uk-tab="animation: uk-animation-slide-left-medium, uk-animation-slide-right-medium">


                    <li><router-link to="/ListClient" class="uk-margin-right">Liste des Clients</router-link></li>
                
                    <li class="uk-text-right"><router-link to="/ListContrat" class="uk-margin-right">Liste des Contrats</router-link></li>
                    
                    <li class="uk-text-right"><router-link to="/AddContrat" class="uk-margin-right">Ajouter un contrat </router-link></li>

                    <li><router-link to="/Vente" class="uk-margin-right">Ajouter un client </router-link></li>


                </ul>

                <ul class="uk-switcher uk-margin">
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li> 

                </ul>
                <div>
                   <h4> Liste des Contrat de  : {{Client }} </h4>
                </div>
                <div v-for="c in Contrat" :key="c.id"  v-if="loading == false">

                    <div class="uk-card uk-card-default uk-margin" >   
                        <div class="uk-card-header">
                            
                            <router-link :to="'/ListContrat'" class="pull-right"><button class="uk-button uk-button-text uk-margin uk-padding-bottom uk-padding-top"><i class="fa fa-arrow-left uk-margin-right"></i>Retour</button></router-link>
                            <h3 class="uk-card-title uk-margin-remove-bottom uk-text-capitalize">{{c.Article}}</h3>
                            <p class="uk-text-meta uk-margin-remove-top">Fait le  : <time date="">{{ `${c.CreatedAt.getDate()}/${c.CreatedAt.getMonth()}/${c.CreatedAt.getFullYear()}`}}</time></p>
                            <hr>
                            <div class="uk-card-body">
                                <p><b>Description :  </b> La vente de {{c.Qte}} {{ c.Article }} pour le client {{c.Client}} <br> Paiement <span v-if="c.Type"> par </span>{{ c.Paiement }} <span v-if="c.Type">de {{ c.Taux }} Dinars Sur  {{ c.Type }} Mois .</span><br>Prix Total :  {{ c.Prix }} Dinars </p>
                            </div>  
                            <div class="uk-card-footer">
                                <button v-on:click="deleteArticle(c.id)" class="uk-button uk-button-secondary uk-margin-left" ><i class="fa fa-trash-o uk-margin-right"></i>Supprimer</button> 
                            </div>     
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import VenteService from '../../../Service/VenteService';

export default {
    name : 'ClientContrat',
    data () {
        return {
            loading : false,
            Contrat : [],
            error : '',
            Client : this.$route.params.Client,
        }
    },
    mounted() {

    },
    async created() {
        this.loading = true
        try{ 
            this.Contrat = await VenteService.GetClientContrat(this.Client)
            this.loading = false
        }catch(error){
            this.error= 'Aucun accs  Internet'
            this.loading = false
        }
    },
}
</script>

