<template>
    <div class="uk-section uk-height-viewport uk-padding uk-margin uk-margin-remove-top uk-section-muted">
        <div class="uk-container">
            <div class="alert alert-danger col-12" v-if="error">{{ error }}</div>
            <div v-if="loading"  class="uk-position-center">
                <div class="loader"></div>
            </div> 

            <div class="uk-card uk-card-default" >   
                <div class="uk-card-header" v-for="c in Contrat" :key="c.id" v-if="loading == false">
                    
                    <router-link :to="'/ListContrat'" class="pull-right"><button class="uk-button uk-button-text uk-margin uk-padding-bottom uk-padding-top"><i class="fa fa-arrow-left uk-margin-right"></i>Retour</button></router-link>
                    <h3 class="uk-card-title uk-margin-remove-bottom uk-text-capitalize">{{c.Client}} - {{c.Article}}</h3>
                    <p class="uk-text-meta uk-margin-remove-top">Fait le  : <time date="">{{ `${c.CreatedAt.getDate()}/${c.CreatedAt.getMonth()}/${c.CreatedAt.getFullYear()}`}}</time></p>
                    <hr>
                    <div class="uk-card-body">
                        <p><b>Description :  </b> La vente de {{c.Qte}} {{ c.Article }} pour le client {{c.Client}} <br> Paiement <span v-if="c.Type"> par </span>{{ c.Paiement }} <span v-if="c.Taux != 0 ">de {{ c.Taux }} Dinars Sur  {{ c.Type }} Mois .</span><br>Prix Total :  {{ c.Prix }} Dinars </p>
                    </div>  
                    <div class="uk-card-footer">
                        <button v-on:click="deleteArticle(c.id)" class="uk-button uk-button-secondary uk-margin-left" ><i class="fa fa-trash-o uk-margin-right"></i>Supprimer</button> 
                    </div>     
                </div>
            </div>
            <div class="uk-alert uk-alert-primary text-center" v-if="alert">{{ alert }}</div>
        </div>
    </div>
</template>

<script>
import VenteService from '../../Service/VenteService';
export default {
    name : 'FindOneContrat',
    data () {
        return {
            loading : false,
            Contrat : [],
            error : '',
            id : this.$route.params.id,
            alert : ''
        }
    },mounted() {

    },
    async created () {
        this.loading = true
        try{
            this.Contrat = await VenteService.getOneArticle(this.id)
            this.loading = false
        }catch(err){
            this.error = 'Aucun accs  Internet'
            this.loading = false
        }

    }
}
</script>

