<template>
    <div>
        <div class="uk-section uk-height-viewport uk-padding uk-margin">
            <div class="uk-container">
                <div class="uk-alert uk-alert-danger col-12" uk-alert v-if="error">{{ error }}</div>
                <div v-if="loading"  class="uk-position-center">
                    <div class="loader"></div>
                </div>
                
                <div class="uk-form-stacked uk-card" v-if="loading == false">
                    <ul uk-tab="animation: uk-animation-slide-left-medium, uk-animation-slide-right-medium">

                    <li class="uk-text-right"><router-link to="/AddContrat" class="uk-margin-right">Ajouter un contrat </router-link></li>

                    <li class="uk-text-right"><router-link to="/vente" class="uk-margin-right">Ajouter un client</router-link></li>

                    <li class="uk-text-right"><router-link to="/ListClient" class="uk-margin-right">Liste des Clients</router-link></li>
                    
                    <li class="uk-text-right"><router-link to="/ListContrat" class="uk-margin-right">Liste des Contrats</router-link></li>



                </ul>

                <ul class="uk-switcher uk-margin">
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li> 
                </ul>

                
                    <div class="uk-form-stacked uk-margin uk-padding blueBar  ">
                        <label>Contrat : </label>
                        <div class="uk-margin">
                            <label class="uk-form-label uk-margin" for="form-stacked-text  ">Nom et prenom du Client: </label>
                            <div class="uk-form-controls">
                                <select class="uk-select" v-on:change="getIndex" v-model="clientindex">
                                    <option v-for="(C,i) in Clients" :key="C.id" :value="i" >
                                        {{ C.Client }} - Cin : {{ C.Cin }}
                                    </option>
                                </select>
                            </div>
                        </div>
                        <div class="uk-margin">
                            <label class="uk-form-label uk-margin" for="form-stacked-text  ">Confirmation du CIN : </label>
                            <div class="uk-form-controls">
                                <input class="uk-input uk-form-width-large uk-width-1-1" id="form-stacked-text" type="number"  v-model="Cin"  placeholder="Numero de la carte d'identit national " required>
                            </div>
                        </div>
                        <div class="uk-margin">
                            <label class="uk-form-label uk-margin" for="form-stacked-text  ">Nom de l'Article : </label>
                            <div class="uk-form-controls">
                                <select class="uk-select" v-on:change="getIndex" v-model="articleindex">

                                    <option v-for="(A, index) in Articles" :key="A.id" :value="index" >
                                        {{ A.Name }} - Quantit : {{ A.Qte }} 
                                    </option>
                                    
                                </select>
                            </div>
                        </div>
                        <div class="uk-margin">
                            <label class="uk-form-label uk-margin" for="form-stacked-text  ">Quantit : </label>
                            <div class="uk-form-controls">
                                <input class="uk-input uk-form-width-large uk-width-1-1" id="form-stacked-text" type="number" v-on:input="CheckQte" v-model="Qte"  placeholder="Quantit" >
                            </div>
                        </div>
                        <div class="uk-margin uk-grid-small uk-child-width-auto uk-grid">
                            <label>Les type de paiement : </label>
                            <label><input class="uk-radio" type="radio" name="Paiement" v-model="Paiement" value="Cash" v-on:change="change"> Cash </label>
                            <label><input class="uk-radio" type="radio" name="Paiement" v-model="Paiement" value="Facilit" v-on:change="change"> Par Facilit </label>
                        </div>
                        <div class="uk-margin uk-grid-small uk-child-width-auto uk-grid uk-animation-slide-right-medium" v-if="show" >
                            <label>Facilit Sur  : </label>
                            <label><input class="uk-radio" type="radio" name="Type" v-model="Type" value="3" v-on:change="clacPrix"> 3 Moin </label>
                            <label><input class="uk-radio" type="radio" name="Type" v-model="Type" value="6" v-on:change="clacPrix"> 6 Moin </label>
                            <label><input class="uk-radio" type="radio" name="Type" v-model="Type" value="9" v-on:change="clacPrix"> 9 Moin </label>
                            <label><input class="uk-radio" type="radio" name="Type" v-model="Type" value="12" v-on:change="clacPrix"> 12 Moin </label>
                        </div>
                        <div class="uk-margin uk-animation-slide-right-medium" v-if="Type && show">
                            <label class="uk-form-label uk-margin" for="form-stacked-text  ">Taux de paiment par mois : </label>
                            <div class="uk-form-controls">
                                <input class="uk-input uk-form-width-large uk-width-1-1" id="form-stacked-text" type="text"  v-model="taux"  placeholder="Nom de l'article ..." required>
                            </div>
                        </div>
                        <div class="uk-margin">
                            <label class="uk-form-label uk-margin" for="form-stacked-text  ">Prix : </label>
                            <div class="uk-form-controls">
                                <input class="uk-input uk-form-width-large uk-width-1-1" id="form-stacked-text" type="text"  v-model="Prix"  placeholder="Nom de l'article ..." required>
                            </div>
                        </div>
                        <div><br>
                            <button v-on:click="createContrat" class="uk-button uk-button-primary">Sauvgarder <i class="fa fa-send-o uk-margin-left"></i></button>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import ArticleService from '../../../Service/ArticleService'
import VenteService from '../../../Service/VenteService';
import ClientService from '../../../Service/ClientService';

export default {
    name : 'AddContrat',
    data () {
        return {
            Clients: [],
            Client : '',
            Articles : [],
            Article : '',
            Qte : 1,
            Prix : 0,
            Type: '',
            Paiement : '',
            show: false,
            loading: false,
            error : '',
            Cin : '',
            PrixAchat :'',
            clientindex:'',
            articleindex:'',
            taux : '',
            msg :''
        }
    },
    mounted() {
    },
    async created() {
        this.loading = true
        try{
            this.Clients = await ClientService.GetClients()
            this.Articles = await ArticleService.getArticle() 
            this.loading = false
        } catch(error){
            this.error = 'Aucun accs  Internet'
            this.loading = false
        }
    },
    methods: {
        change : function(){
            if( this.Paiement == 'Facilit' ){
                this.show  = true 
            }else{
                this.show = false
                this.taux = 0
                this.Prix = parseInt(this.Article.Prix) + parseInt((this.Article.Prix * 15)/100)
                this.Prix = this.Prix * this.Qte
            }
        },        
        CheckQte : function (){
            if(this.Qte < 0 ){
                this.Qte = 1
            }
            if(this.Qte > this.Article.Qte){
                this.error = 'La Quantit  vendre est superieur de la quantit au stock'
                this.Qte = this.Article.Qte
            }
        },
        clacPrix : function(){
            if(this.Type == 3){
                this.Prix =''
                let g = parseInt(this.Article.Prix) + parseInt((this.Article.Prix * 15)/100) 
                this.Prix =  parseInt(g) + parseInt((g * this.Type)/100)
                this.Prix =  this.Prix * this.Qte
                this.taux = this.Prix / 3    
                this.taux = Math.round(this.taux*100)/100
            }
            if(this.Type == 6){
                this.Prix =''
                let g = parseInt(this.Article.Prix) + parseInt((this.Article.Prix * 15)/100) 
                this.Prix =  parseInt(g) + parseInt((g * 5)/100)
                this.Prix =  this.Prix * this.Qte            
                this.taux =  this.Prix / 6
                this.taux = Math.round(this.taux*100)/100

            }
            if(this.Type == 9){
                this.Prix =''
                let g = parseInt(this.Article.Prix) + parseInt((this.Article.Prix * 15)/100) 
                this.Prix =  parseInt(g) + parseInt((g * 7)/100)
                this.Prix =  this.Prix * this.Qte
                this.taux = this.Prix / 9
                this.taux = Math.round(this.taux*100)/100

            }
            if(this.Type == 12){
                this.Prix =''
                let g = parseInt(this.Article.Prix) + parseInt((this.Article.Prix * 15)/100) 
                this.Prix =  parseInt(g) + parseInt((g * 9)/100)
                this.Prix =  this.Prix * this.Qte
                this.taux = parseInt(this.Prix / 12)
                this.taux = Math.round(this.taux*100)/100
            }       
        },
        getIndex : function(){
            this.Client = this.Clients[this.clientindex]
            this.Article = this.Articles[this.articleindex]
            if( this.Article ){
                this.Prix = this.Article.Prix
            }
        },
        changeStat : function(){
            this.msg = ''
        },
        async createContrat(){
            try{
                this.loading = true
                if(this.Client.Cin != this.Cin){
                    this.msg = 'Les numro de la Cin ne sont pas identitque ! '
                    this.loading = false
                }else{
                    await VenteService.SetVente(this.Client.Client,this.Client.Cin,this.Article.Name,this.Qte,this.Paiement,this.Type,this.taux,this.Prix)
                    this.Client = ''
                    this.Cin = ''
                    this.Article =''
                    this.Qte = 1
                    this.msg='le contrat  t cre'
                    this.$router.push('/ListContrat')
                    this.loading = false
                }
            }catch(error){
                this.error = error
                this.loading = false
            }
        }
    }

}
</script>

<style>
.blueBar{
    border-left: 5px rgba(33,150,243) solid;    
}
</style>
