<template>
<div class="uk-section uk-height-viewport uk-padding uk-margin uk-section-muted uk-margin-remove-top">
  <div class="">
    <div class="uk-padding uk-margin uk-margin-left uk-position-center uk-width-1-2">
      <div class="uk-alert-danger" uk-alert v-if="error">{{ error }}</div>
      <div v-if="loading"  class="uk-position-center">
        <div class="loader"></div>
      </div>
    </div>
    <div class="uk-grid" uk-grid>

        <div class="uk-width-1-4  uk-margin-large uk-margin-large-top ">
            <p> Liste des Article : </p>
            <ul class="uk-list uk-list-striped">
                <li class="" 
                    v-for="com in cm" :value="com.Name" :key="com.id" >
                    <a v-on:click="getStat(com.Name)"> 
                        {{com.Name}}
                    </a>
                    <span class="pull-right">
                        <span class="uk-badge">
                            {{com.Qte}}
                        </span>
                    </span>
                </li>
            </ul>
        </div>
        <div class="small uk-width-1-2"  v-if="loading == false">

            <div class="uk-height-max-small uk-width-3-4">
                <BarChart :chart-data="datacollection" :styles="{height: '250px'}" ></BarChart>
            </div>
        </div>
    </div>
  </div>
</div>
</template>

<script>
import BarChart from '../../assets/BarChart.js'
import CommandeService from '../../Service/CommandeService'
import ArticleService from '../../Service/ArticleService'

  export default {
    components: {
        BarChart
    },
    data () {
        return {
            datacollection: null,
            commandetmp : [],
            loading : false,
            commande : [],
            type : '',
            error : '',
            cm : [],
        }      
    },

    async created() {
    },
     async mounted () {
        try{
            this.loading = true
            this.commande = await CommandeService.getCommande();
            this.cm = await ArticleService.getArticle()
            this.loading = false
        }catch(err){
            this.error = 'Aucun accs  Internet';
            this.loading = false
        }
    },
    methods: {
        getStat(nom){
            this.commandetmp =[]
            for(let j = 0 ; j<this.commande.length;j++ ){
                if(this.commande[j].Article === nom ){
                    this.commandetmp.push(this.commande[j])
                }
            }
            this.fillData()
            let object = {

                backgroundColor: [],
                data: [],
                label : []
            }
            for (let index = 0; index < this.commandetmp.length; index++) {
                let mydate = this.commandetmp[index].CreatedAt.getDate()+"/"+this.commandetmp[index].CreatedAt.getMonth()+"/"+this.commandetmp[index].CreatedAt.getFullYear()
                this.datacollection.labels.push(mydate)
                object.data.push(this.commandetmp[index].Qte)
                object.backgroundColor.push(this.getRandomColor())

            }
            let sm = " "                
            object.label.push(sm)
            this.datacollection.labels.push("")
            object.data.push(0)
            object.backgroundColor.push(this.getRandomColor())
            this.datacollection.datasets.push(object)
        },
        
        fillData () {
            this.datacollection = {
                labels: [],
                datasets: [],
                backgroundColors: []
            }
        },

        getRandomColor() {
            var letters = '0123456789ABCDEF';
            var color = '#';
            for (var i = 0; i < 6; i++) {
                color += letters[Math.floor(Math.random() * 16)];
            }
            return color;
        },
    }
}
</script>

<style>
  .small {
    max-width: 300px;
    margin:  10px auto;
  }
</style>